.contact{
    height: 100vh;
    position: relative;
}

.bg{
    width: 20px;
    height: 100%;
    background-color:cadetblue;
    position: absolute;
}

.wrapper{
    padding: 50px;
    display: flex;
}

.left{
    flex: 1;
}

.right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.title{
    font-size: 60px;
    width: 80%;
}
.item{
    display: flex;
    align-items: center;
    margin: 50px 0px;
    font-weight: 300;
    width: 70%;
    font-size: 30px;
}

.icon{
    width: 50px;
    height: 50px;
    margin-right: 20px;
    
}

.description{
    font-weight: 200;
}

form {
    margin-top: 20px;
  }
  
  input {
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid black;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
  }
  
  textarea {
    width: 100%;
    margin: 10px 0px;
    font-size: 14px;
    padding-left: 10px;
  }
  
  button {
    border: none;
    padding: 15px;
    background-color: #59b256;
    color: white;
    font-weight: 500;
    cursor: pointer;
  }
  
  @media screen and (max-width: 480px) {
    .wrapper {
      flex-direction: column;
      padding: 0px 50px;
    }
  
    .title {
      font-size: 30px;
    }
  
    .item {
      margin: 20px 0px;
      width: 100%;
    }
  
    .description {
      display: none;
    }
  
    form {
      margin-top: 0px;
      display: flex;
      flex-wrap: wrap;
    }
  
    input {
      width: 35%;
      height: 40px;
      margin: 10px;
      margin-left: 0;
    }
  
    button {
      margin-top: 10px;
    }
  }