.about{
    height: 100vh;
    display: flex;
    align-items: center;
    font-size: 50px;
    /* background-color: #555; */
}

.a-left{
    flex:1;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 100%;
}

.a-right{
    flex:1
}
.a-card.bg{
    position: absolute;
    top: 50px;
    left: 50px;
    background-color: #333;

}

.a-card{
    width: 60%;
    height: 70vh;
    border-radius: 30px;
    border-radius: 30px;
    position: relative;
    overflow: hidden;
}

.a-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.aboutme-img{
    width: 400px;
    height: 400px;
    border-radius: 20px;
}

.a-title{
    font-weight: 400;
}
.a-sub{
    margin: 20px 0px;
}

.a-description{
    font-weight: 300;
}

.a-aboutme{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.aboutme-text{
    width: 70%;
}

.codingtitle{
    font-weight: bold;
    color: #555;
    margin-bottom: 10px;
}


@media screen and (max-width:480px) {
    .a{
      flex-direction: column;
      text-align: center;
      margin-top: 50px;
    }

    .a-left {
      width: 100%;
    }

    .a-card {
      height: 30vh;
    }

    .a-card.bg, .a-award {
      display: none;
    }

    .a-right{
      padding: 20px;
    }
}